.posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  margin: 0;
  padding: 20px 0 20px 20px;
  scroll-behavior: smooth;
}
.posters::-webkit-scrollbar {
  display: none;
}
.poster {
  object-fit: contain;
  margin-right: 10px;
  transition: transform 450ms;
  border-radius: 10px;
}
.poster:hover {
  transform: scale(1.08);
}

.row_title {
  border-radius: 5px;
  background-color: rgb(182, 79, 182);
  color: white;
  padding: 3px;
  margin: 3px;
  font-size: 16px;
  text-align: center;
  text-shadow: 2px 6px 2px gray;
}



